
.main {
  width: 100vw;
  background-image: linear-gradient(
    0deg,
    #000000 0%,
    #d3d3d3 79%,
    #ffffff 100%
  );
  padding: 5px;
  
}
.home h1 {
  font-family: "Neusharp Bold";
}

/* HeaderImage.css */
.header-image-container {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.header-image {
  max-width: 1070px;
  height: auto;
}

.video-box{
 display: flex;
 justify-content: flex-end;
}

.scroll-indicator {
  width: 200px;
  background: none;
  position: absolute;
  bottom: 20px; /* Adjust as needed */
  left: 100px;
  border-top: 2px solid #fff;
  text-align: center;
  color: #fff; /* Text color */
  font-size: 12px; /* Adjust font size as needed */
  transform: rotate(-90deg); /* Rotate the text by 90 degrees */
  transform-origin: left bottom; /* Rotate from the left bottom corner */
}

.scroll-indicator div {
  width: fit-content;
  text-align: left;
  border-top: 5px solid #fff;
}

section {
  width: 100vw;
  background-image: linear-gradient(0deg, #000000 0%, #000000 100%);
  color: white;
}

.header-content{
    /* margin: 0px 5rem; */
    margin-right: 5rem;
    padding-top: 4rem;
}

section h1 {
    padding-top: 2rem;
    width: fit-content;
  font-size: 4rem;
  letter-spacing: -3px;
  color: #ffffff;
  font-weight: bold;
  font-family: "Neusharp Bold";
  padding-left: 5rem;
}

video {
    width: 75%;
    /* max-width: 1000px; */
    display: block;
    position: relative;
    right: -5rem;
    top: -5rem;
    z-index: 5;
    /* margin: 20px auto; */
  }

  .dynamicHr .dark-hr{
    background-color:#bfbfbf
};

.dynamicHr .light-hr{
    background-color:#ffffff33
}

.slider-hr{
  position: absolute;
  width: 100%;
  z-index: 0;
  border: 1px solid #29282830;
  white-space: 3px;
  top: 3px;
}

.carousel-dot {
  width: 15px;
  height: 8px;
  background-color: black;
  border-radius: 5px;
  margin: 0 1px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  z-index: 1;

}

  /* Adjust font size for smaller screens */
  @media screen and (max-width: 1200px) {
  section h1{
    font-size: 3rem;
  }
  .header-content{
    margin-left:5rem ;
  }

  .video-box{
    justify-content: center;
  }

  video{
    margin: 20px auto;
    position: unset;
    width: 100%;
  }

  .about-section{
    display: block;
  }
   .about-uplift{
    top: 0rem;
   }
   .card-container{
    justify-content: center;
   }
   }

  /* Adjust font size for smaller screens */
@media screen and (max-width: 1100px) {
 .footerVerticals{
  display: none;
 }
 .features{
  display: none;
 }

}
