.footer{
    margin-top: 5rem;
    font-family: "Helvetica Now Display","sans-serif";
    font-weight: 500;
    font-size: 1rem;
}

.org-title{
    background-color:  #f6f6f6;;
    display: flex;
    justify-content: center;
    align-items: center;
}

.org-title img{
    width: 18rem;
}

.org-title span{
    
    color: #8C1A1B;
    border-left: 2px solid #8C1A1B;
    padding: 1rem;
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: 600;
    font-family: auto;
    line-height: 2rem;
}
.org-title img{
    height: 200px;
    width: auto;object-fit: contain;
}

.org-details{
    display: flex;

}

.org-address{
    flex:1;
    padding: 2rem 4rem;
    background-color: black;
    color: white;
}

.address p{
    padding: 2px;
}

.contact-info{
    margin-top: 10px;
}

.contact-info p{
    padding: 5px;
    margin-top: 4px;
    display: flex;
    align-items: center;
    gap: 1rem;
}
.contact-info i {
    color: #eca404;
}

.org-links-container{
    flex:1;
  padding: 2rem 4rem;
    background-color: #eca404;
    
}

.org-links{
    
    max-width: 400px;
    margin: auto;
background-color: #eca404;

;
}
.footer-links {
    display: grid;
  grid-template-columns: repeat(3, 1fr); /* Create three equal-width columns */
  gap: 2px; /* Add some spacing between columns */
  font-weight: bold;
  }

  .footer-links{
    list-style: none;
    /* border-left: 2px solid black; */
  }

  .footer-links li {
    padding: 0rem 0.5rem;
    border-left: 2px solid black;
  }

.page-links a {
    text-decoration: none;
    color: inherit;
    
}
.social-links{
    padding: 1rem;
    display: flex;
    margin-top: 2rem;
    gap: 1rem;
    justify-content: center;
}
.social-links img{
    width: 50px;
    
}

.copyright{
    background-color: #252525;
    color:#ffffff;
    display: flex;
    justify-content: space-between;
    padding:2rem 3rem;
    letter-spacing: 0.5px;
    font-size: 14px
    ;
}

/* Adjust font size for smaller screens */
@media screen and (max-width: 1000px) {
    .org-details{
        flex-wrap: wrap;
    }
    .address{
        text-align: center;
    }
}
/* Adjust font size for smaller screens */
@media screen and (max-width: 768px) {
    .copyright{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
    }
}

/* Further adjustments for even smaller screens if needed */
@media screen and (max-width: 576px) {
  .copyright{
    font-size: 0.7rem;
  }
  .social-links img{
    width: 40px;
    
}
.org-title span{
    font-size: 1rem;
}


}