/* TabComponent.css */

/* Container for the entire component */
.tab-component {
  margin: 4rem 0rem;
}

/* Tabs container */
.tabs {
  display: flex;
  justify-content: center;
  gap: 5rem;
  margin-bottom: 30px;
}

/* Individual tab button */
.tab-button {
  border: none;
  background: none;
  font-size: 1.5rem;
  cursor: pointer;
  outline: none;
}

/* Styling for the active tab button */
.tab-button.active {
  border-bottom: 5px solid #eca404;
}

/* Container for the card list */
.card-list {
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  gap: 20px;
  transition: transform 0.3s ease-in-out;
}

/* Individual card styling */
.card {
  flex: 1;

  padding: 20px;
  border-radius: 10px;
  /* filter: drop-shadow(0 0 54.5px rgba(214, 214, 214, 0.45)); */
  background-color: #ffffff;
  min-width: 230px;
}

/* Card title */
.card h3 {
  font-size: 1.5rem;
  margin: 0;
}

/* Card content */
.card p {
  font-size: 1rem;
  margin: 10px 0;
}

.card-hr {
  border: none;
  height: 3px;
  width: 30%;
  background-color: #eca404;
  margin: 1rem 0;
}

.article-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 2rem;
}
.article-card-footer a {
  color: #032648;
  margin-bottom: 0.2rem;
  border-bottom: 2px solid #032648;
}

.content-wrapper {
  margin: 0rem 6rem;
  width: calc(100vw - 12rem);
  overflow: hidden;
  display: flex;
  background-color: #f6f6f6;
  
  position: relative;
  
}

@media screen and (max-width: 425px) {
  
 .content-wrapper{
  margin: 0rem auto;
  overflow: visible;
  justify-content: center;
 }
  }
