.aboutSection {
    font-family: "Helvetica Now Display","sans-serif";
    color: #ffffff;
    display: flex;
    justify-content: center;
}

.about-content{
  position: relative;
  /* top: -12rem; */
}
.about-content::before {
  content: '';
  position: absolute;
  width: 0px;
  height: 238px;
  border-right: 1px solid #ffffff4d;
  /* border: 1px solid #ffffff4d; */
  bottom: 0px;
  right: 0px;
}
.about-detail{
  margin-left: 5rem;
}
.about-uplift{
  position: relative;
  top: -12rem;
  border-right: 1px solid #ffffff4d;
}
.aboutSection h1 {
  margin: 0;
  font-size: 5rem;
  padding-left: 0rem;
  font-family: "Neusharp Bold"
}

.aboutSection h4 {
  font-size: 1.5rem;
  font-weight: 200;
  letter-spacing: 0.5rem;
  margin-bottom: -20px;
}
.aboutSection h4 span {
  color: yellow;
  letter-spacing: 0px;
}

.aboutSection h5{
    font-size: 1.5rem;
    font-weight: 300;
}


.aboutSection p{
    padding-top: 1rem;
    line-height: 1.5rem;
    /* font-size: 1.1rem; */
}


.know-more-button {
  border: 1px solid #ffffff4d;
    padding: 1.5rem 1rem;
    border-radius: 0px;
    width: 100%;
    border-left: none;
    border-right: none;
    background-color: #ffc107; /* Yellow background color */
    background: none;
    color: #ffffff;
    /* padding: 10px 0px;  */
    margin: 1rem 0;
    
    
    font-size: 1rem; /* Adjust font size as needed */
    cursor: pointer;
     /* Adjust margin as needed */
  }

  
  .yellow-square {
    width: 20px;
    height: 20px;
    background-color: #ffc107; /* Yellow square color */
    display: inline-block;
    margin-left: 5px; /* Adjust margin as needed */
  }


  .card-container{
    display: flex;
    
  }


    /* Adjust font size for smaller screens */
    @media screen and (max-width: 1200px) {
      
    
          
      .aboutSection{
        display: block;
      }
       .about-uplift{
        top: 0rem;
       }
       .card-container{
        justify-content: center;
        border-right: 1px solid #ffffff4d;
       }
       .about-content::before {
        content: '';
        position: absolute;
        width: 0px;
        height: 0px;
        border-right: none;
        /* border: 1px solid #ffffff4d; */
        bottom: 0px;
        right: 0px;
      }
       }