
.videoSection{
    padding: 4rem 0rem ;
    
}
.videoSection header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 0rem 6rem;
}
.videoSection header h1{
    font-size: 3rem;
}

.videoSection header p,.videoSection header h1{
    font-family: "Neusharp Bold";
}

.videoSection header button{
    background: #eca404;
    border: none;
    padding: 1.3rem 2rem;
    font-size: 1.2rem;
    color: #fff;
    border-radius: 5px;
}

.videoSection .video-card-list{
    display: flex;
    transition: transform 0.3s ease-in-out;
    padding: 0.5rem;
    gap: 20px;
}

.video-wrapper{
    margin: 0rem 6rem;
    width: calc(100vw - 12rem);
    overflow: hidden;
    display: flex;
    filter: drop-shadow(0 0 54.5px rgba(214,214,214,0.45));
    overflow: hidden;
    position: relative;
}

@media screen and (max-width: 425px) {
  
 .video-wrapper{
    margin: auto;
    width: 100%;
    overflow: visible;
    justify-content: center;
 }

 .video-card-list{
    justify-content: center;
 }
    }

 /* Further adjustments for even smaller screens if needed */
 @media screen and (max-width: 370px) {
    .videoSection{
        padding: 4rem 0rem ;
        text-align: center;
    }
    .videoSection header{
        display: block;
    }
  }