.footerVerticals{
    width: 100vw;
  }
  .footerVerticals > div{
      display: flex;
      justify-content: space-around;
      align-items: center;
      gap: 2rem;
      padding: 3rem 5rem;
  }
  
  .supportUs {
    background-color: #eca404;
  }
  
  .supportUs > div {
    max-width: 39%;
    color: #382224;
    font-weight: 600;
    letter-spacing: 0.3px;
  }
  
  article {
    text-wrap: nowrap;
    color: #900000;
    font-weight: bold;
  }
  
  article > h1 {
    font-size: 3.5rem;
    font-family: "Neusharp Bold";
  }
  
  .supportUs button {
    padding: 1.5rem 3.5rem;
    text-wrap: nowrap;
    border: none;
    color: #fff;
    font-size: 1.2rem;
    letter-spacing: 1px;
    font-family: "Helvetica Now Display", "sans-serif";
    font-weight: 600;
    border-radius: 5px;
    background-color: #900000;
  }
  
  
  .whoWeAre{
      background-color: #020202;
  }
  
  .whoWeAre article{
      color: #ffffff;
  }
  
  .whoWeAre > div{
      color: white;
      max-width: 39%;
      font-weight: 600;
      letter-spacing: 0.3px;
      line-height: 1.5rem;
  }