/* Navbar.css */
/* Normal navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  padding-top: 2rem;
}

.navbar-logo {
  display: flex;
  align-items: center;
}
.navbar-logo span {
  padding-left: 0.7rem;
  border-left: 2px solid #870f11;
  height: fit-content;
  font-weight: bold;
  font-family: auto;
  font-size: 0.8rem;
  color: #870f11;
  text-transform: uppercase;
}

.navbar-logo img {
  width: 100px;
  object-fit: contain;
  margin-right: 10px;
}

.navbar-menu {
  display: flex;
  list-style: none;
  gap: 1rem;
}

.navbar-menu li {
  margin-right: 20px;
  position: relative;
  cursor: pointer;
}

.navbar-menu a {
  text-decoration: none;
  font-size: 1rem;
  line-height: 39px;
  color: #382224;
  font-weight: 600;
}

.FromDeskOfPresidentPage{
  gap: 6rem  ;
  padding-top: 0rem;
  padding: 2rem 20px;
}
.FromDeskOfPresidentPage ul{
  flex: 1;
  justify-content: space-between;
}

.FromDeskOfPresidentPage .navbar-menu a{
color: black;
font-weight: 500;
font-size: 0.9rem;
}

/* Navbar.css */
/* ... Existing CSS */

.navbar-item {
  position: relative;
}

.submenu {
  z-index: 1;
  width: 200px;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  list-style: none;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 0;
  margin: 0;
  
}
.submenu li{
  padding: 7px;
  margin-right: 0rem;

 }

 .submenu li:hover{
  background-color: #f2eaea;
 }

 .navbar-menu li:hover  .submenu {
  display: block;
}


/* Hamburger menu styles for small screens */
@media screen and (max-width: 768px) {
  .navbar-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 0;
    background-color: #fff;
    z-index: 1;
  }

  .navbar-menu.active {
    display: flex;
  }

  .navbar-menu li {
    margin: 10px 0;
  }

  .navbar-hamburger {
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }

  .bar {
    width: 25px;
    height: 3px;
    background-color: #fff;
    margin: 3px 0;
  }

  .bar.open {
    transform: rotate(-45deg) translate(-5px, 5px);
  }
}
