/* Carousel Dots */
.carousel-dots {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  position: relative;
}

.features .carousel-dot {
  width: 15px;
  height: 8px;
  background-color: #ccc;
  border-radius: 5px;
  margin: 0 1px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  z-index: 1;

}

.carousel-dot.active-dot {
  background-color: rgb(236, 164, 4);
  width: 35px; /* Change to your desired active dot color */
  
    
}
/* Carousel Wrapper */
.card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.features.slider-hr{
  position: absolute;
  width: 100%;
  z-index: 0;
  border: 1px solid #ffffff2e;
  
  top: 3px;
}