
.icprdEvents{
    /* padding: 4rem 6rem ; */
    
}
.icprdEvents header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 1rem 6rem;
}
.icprdEvents header h1{
    font-size: 3.5rem;
}
.event-card-wrapper{
    margin: 0rem 6rem;
    width: (100vw-12rem);
    
    /* display: flex; */

 
  overflow: hidden;
  position: relative;
}
.icprdEvents header p,.icprdEvents header h1{
    font-family: "Neusharp Bold";
}

.icprdEvents header button{
    background: #eca404;
    border: none;
    padding: 1.3rem 2rem;
    font-size: 1.5rem;
    color: #fff;
    border-radius: 5px;
}

.icprdEvents .event-section{
    display: flex;
    justify-content: center;
    /* overflow: hidden; */
    transition: transform 0.3s ease-in-out;

}

@media screen and (max-width: 425px) {
  
  .event-card-wrapper{
    margin: 0;
  }
  .icprdEvents header{
    text-align: center;
    display: block;
  }
  }
 /* Further adjustments for even smaller screens if needed */
 @media screen and (max-width: 370px) {
    .icprdEvents{
        padding: 4rem 0rem ;
        text-align: center;
    }
    .icprdEvents header{
        display: block;
    }
  }