.banner-text{
    top: 20px;
    bottom: unset;
}

.economic-facts{
    display: flex;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin:  auto;
    margin-top: 6rem;
    max-width: 1200px;
    
}

.economic-facts > div{
    display: flex;
    gap: 1rem    ;
    padding: 0rem 3rem;
}

.economic-facts > div:nth-child(2){
   border-left: 1px solid gray;
   border-right: 1px solid gray;
}

.economic-facts > div > h1{
    
    font-size: 3rem;
    color: grey;
}

.economic-facts > div > p{
    
    font-size: 1.5rem;
    font-weight: bolder;
    
}

.ecopolicy{
    padding: 1rem 5rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 0.9rem;
    font-weight: 400;
}

.economicGraph{
    text-align: center;
}

.economicGraph img{
    max-width: 965px;
    margin: auto;
}